<template>
    <div>
        <el-main>
            <div class="select">
                <el-input v-model="id" placeholder="账号" clearable></el-input>
                <el-input v-model="name" placeholder="姓名" clearable></el-input>
            </div>
            <div class="btn">
                <el-button type="primary" icon="el-icon-plus" @click="add()">添加</el-button>
            </div>
            <el-table
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'id'}"
                :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
            >
                <el-table-column prop="id" label="账号" sortable></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="role" label="角色"></el-table-column>
                <el-table-column
                    prop="state"
                    label="状态"
                    min-width="100"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.state === true ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.state==true?'已启用':'已停用'}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                layout="prev, pager, next"
                :total="1000"
            ></el-pagination>
        </el-main>
        <pm :pmitem="pmitem" :pmVisible="pmVisible" @close-dialogStatus="Close_dialog"></pm>
        <detail
            :detailVisible="detailVisible"
            @close-dialogStatus="Close_dialog"
        ></detail>
    </div>
</template>

<script>
import pm from "@/components/dialog/pm";
import detail from "@/components/dialog/detail";
export default {
    data() {
        return {
            pmitem: {},
            pmVisible: false,
            detailVisible: false,
            ip: "",
            time: "",
            id:"",
            name: "",
            roles: "",
            search: "",
            tableData: [
                {
                    id: "12345",
                    name: "admin",
                    role: ["超级管理员"],
                    state: true,
                    power: ""
                },
                {
                    id: "233333",
                    name: "kkk",
                    role: ["部门1管理员",",","部门2管理员"],
                    state: true,
                    power: ""
                },
                {
                    id: "666666",
                    name: "aaa",
                    role: ["部门3管理员"],
                    state: true,
                    power: ""
                },
                {
                    id: "99999",
                    name: "ccccc",
                    role: ["部门1管理员",",","部门3管理员"],
                    state: true,
                    power: ""
                },
                {
                    id: "1564586845",
                    name: "CSfdsgfs",
                    role: ["部门2管理员"],
                    state: true,
                    power: ""
                },
                {
                    id: "43676587",
                    name: "eftrghy",
                    role: ["部门1管理员"],
                    state: false,
                    power: ""
                },
                {
                    id: "654123698",
                    name: "safds",
                    role: ["部门1管理员",",","部门2管理员",",","部门3管理员"],
                    state: false,
                    power: ""
                }
            ]
        };
    },
    components: {
        pm,
        detail
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        Close_dialog(val) {
            this.pmVisible = false;
            this.detailVisible = false;
        },
        handleEdit(index, row) {
            this.pmitem = row;
            this.pmVisible = true;
        },
        handleDelete(index, row) {
            this.$confirm("是否删除该管理员?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$message({
                    type: "success",
                    message: "删除成功!"
                });
                for (let i in this.tableData) {
                    if (this.tableData[i] == row) {
                        this.tableData.splice(i, 1);
                    }
                }
            });
        },
        add() {
            this.detailVisible = true;
        }
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.select, .el-input {
    width: 156px;
}
.select {
    display: inline;
}
.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}
.search {
    position: relative;
    top: -1px;
}
</style>


